import * as XLSX from 'xlsx';
import { Accessory } from './accessoryTypes';
import { Timestamp } from 'firebase/firestore';

export const importAccessories = async (file: File): Promise<Accessory[]> => {
  const data = await file.arrayBuffer();
  const workbook = XLSX.read(data);
  const sheet = workbook.Sheets[workbook.SheetNames[0]];
  const json: any[] = XLSX.utils.sheet_to_json(sheet);

  return json.map((row) => {
    const type = row.type === 'bulk' ? 'bulk' : 'tracked'; // m·∫∑c ƒë·ªãnh l√† tracked n·∫øu kh√¥ng r√µ
    const accessory: Accessory = {
      id: '',
      companyId: row.companyId || '',
      name: row.name || '',
      type,
      code: type === 'tracked' ? row.code || '' : undefined,
      quantity: type === 'bulk' ? parseInt(row.quantity || '0') : undefined,
      status: row.status || 'in_stock',
      importDate: row.importDate
        ? Timestamp.fromDate(new Date(row.importDate))
        : Timestamp.fromDate(new Date()),
      notes: row.notes || '',
    };
    return accessory;
  });
};
