'use client';

import { useState } from 'react';
import { Accessory } from '@/src/lib/accessories/accessoryTypes';
import { Button } from '@/src/components/ui/button';
import AccessoryExportForm from '@/src/components/accessories/AccessoryExportForm';
import { Dialog, DialogContent, DialogTrigger, DialogTitle } from '@/src/components/ui/dialog';

interface Props {
  accessories: Accessory[];
  onEdit: (item: Accessory) => void;
  onDelete: (id: string) => void;
}

export default function AccessoryTable({ accessories, onEdit, onDelete }: Props) {
  const [selectedAccessory, setSelectedAccessory] = useState<Accessory | null>(null);

  return (
    <div className="overflow-x-auto">
      <table className="w-full border text-sm">
        <thead>
          <tr className="bg-gray-100">
            <th className="p-2 text-left">Name</th>
            <th className="p-2 text-left">Type</th>
            <th className="p-2 text-left">Code / Quantity</th>
            <th className="p-2 text-left">Status</th>
            <th className="p-2 text-left">Import Date</th>
            <th className="p-2 text-left">Export Date</th>
            <th className="p-2 text-left">Notes</th>
            <th className="p-2 text-left">Actions</th>
          </tr>
        </thead>
        <tbody>
          {accessories.map((a) => (
            <tr key={a.id} className="border-t hover:bg-gray-50">
              <td className="p-2">{a.name}</td>
              <td className="p-2 capitalize">{a.type}</td>
              <td className="p-2">
                {a.type === 'tracked' ? a.code : `${a.quantity ?? 0} pcs`}
              </td>
              <td className="p-2 capitalize">{a.status}</td>
              <td className="p-2">
                {a.importDate?.toDate().toLocaleDateString('en-GB') || '-'}
              </td>
              <td className="p-2">
                {a.exportDate?.toDate().toLocaleDateString('en-GB') || '-'}
              </td>
              <td className="p-2 max-w-[200px] truncate">{a.notes || '-'}</td>
              <td className="p-2 flex flex-wrap gap-2">
                <Button size="sm" onClick={() => onEdit(a)}>Edit</Button>
                <Button size="sm" variant="destructive" onClick={() => onDelete(a.id)}>Delete</Button>
                <Dialog>
                  <DialogTrigger asChild>
                    <Button size="sm" variant="secondary" onClick={() => setSelectedAccessory(a)}>
                      Export
                    </Button>
                  </DialogTrigger>
                  <DialogContent className="max-w-xl">
                    <DialogTitle>Export Accessory</DialogTitle>
                    <AccessoryExportForm
                      defaultAccessory={{ id: '', name: a.name }}
                      onComplete={() => setSelectedAccessory(null)}
                    />
                  </DialogContent>
                </Dialog>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}